import React, { useState, useEffect, useRef } from 'react';
import { Surah } from '../../types';
import { fetchSurahs } from '../../services';
import { LoadingSpinner, ErrorMessage } from '../common';
// Removed language-dependent naming for list; always show English alongside Arabic

interface SurahListProps {
  onSurahSelect?: (surah: Surah) => void;
  selectedSurahId?: number;
  className?: string;
}

export const SurahList: React.FC<SurahListProps> = ({
  onSurahSelect,
  selectedSurahId,
  className = ''
}) => {
  const [isFocused, setIsFocused] = useState(false);
  const listRef = useRef<HTMLDivElement>(null);
  const [allSurahs, setAllSurahs] = useState<Surah[]>([]);
  const [surahs, setSurahs] = useState<Surah[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  

  useEffect(() => {
    const loadSurahs = async () => {
      try {
        setLoading(true);
        setError(null);
        const surahsData = await fetchSurahs();
        setAllSurahs(surahsData);
        setSurahs(surahsData);
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Failed to load Surahs');
      } finally {
        setLoading(false);
      }
    };

    loadSurahs();
  }, []);

  const handleRetry = () => {
    setError(null);
    setSurahs([]);
    setLoading(true);
    // Re-trigger the effect
    window.location.reload();
  };

  const getSurahName = (surah: Surah): string => {
    return surah.englishName;
  };

  if (loading) {
    return (
      <div className={`flex justify-center py-8 ${className}`}>
        <LoadingSpinner size="md" text="Loading Surahs..." />
      </div>
    );
  }

  if (error) {
    return (
      <div className={className}>
        <ErrorMessage
          title="Failed to Load Surahs"
          message={error}
          onRetry={handleRetry}
        />
      </div>
    );
  }

  return (
    <div 
      ref={listRef}
      className={`space-y-2 ${className} ${isFocused ? 'ring-2 ring-primary-500 dark:ring-accent-400' : ''}`}
      tabIndex={0}
      onKeyDown={(e) => {
        if (e.key === 'Enter') {
          e.preventDefault();
          setIsFocused(!isFocused);
        }
      }}
      onFocus={() => setIsFocused(true)}
      onBlur={() => setIsFocused(false)}
      aria-label="Surah list"
    >
      {/* Quick filter + AZ jump */}
      <div className="mb-2 space-y-2">
        <input
          type="text"
          placeholder="Filter surahs by name (EN/BN/AR) or number..."
          onChange={(e) => {
            const raw = e.target.value;
            const value = raw.toLowerCase();
            if (!value) {
              setSurahs(allSurahs);
              return;
            }
            const normalizeArabic = (text: string) => text.replace(/[\u064B-\u0652\u0670\u06D6-\u06ED]/g, '');
            const numeric = raw.replace(/\D/g, '');
            const normalizedQuery = value
              .replace(/\b(surah|sura|chapter|the|al)\b/g, '')
              .replace(/\s+/g, ' ')
              .trim();
            const filtered = allSurahs.filter(s => {
              const en = s.englishName?.toLowerCase?.() || '';
              const bn = s.banglaName?.toLowerCase?.() || '';
              const ar = normalizeArabic(s.name || '').toLowerCase();
              const tr = s.transliteratedName?.toLowerCase?.() || '';
              const idStr = String(s.id);
              return (
                en.includes(normalizedQuery) ||
                bn.includes(normalizedQuery) ||
                tr.includes(normalizedQuery) ||
                ar.includes(normalizeArabic(normalizedQuery)) ||
                (numeric.length > 0 && idStr.startsWith(numeric))
              );
            });
            setSurahs(filtered);
          }}
          className="w-full px-3 py-2 text-sm rounded border border-gray-300 dark:border-gray-600 bg-white dark:bg-gray-800 text-gray-900 dark:text-white"
        />
        <div className="flex flex-wrap gap-1 text-xs">
          {Array.from({ length: 12 }, (_, i) => i + 1).map(group => (
            <a 
              key={group} 
              href={`#surah-group-${group}`} 
              className="px-2 py-1 rounded bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-200 hover:bg-gray-200 dark:hover:bg-gray-600"
            >
              {group * 10 - 9}-{Math.min(group * 10, 114)}
            </a>
          ))}
        </div>
      </div>
      {surahs
        .slice()
        .sort((a, b) => a.id - b.id)
        .map((surah, idx, arr) => {
          const currentGroup = Math.floor((surah.id - 1) / 10);
          const prevGroup = idx > 0 ? Math.floor((arr[idx - 1].id - 1) / 10) : -1;
          const showHeader = idx === 0 || currentGroup !== prevGroup;
          return (
            <React.Fragment key={surah.id}>
              {showHeader && (
                <div id={`surah-group-${currentGroup + 1}`} className="pt-2 text-xs font-semibold text-gray-500 dark:text-gray-400">
                  Surahs {currentGroup * 10 + 1}-{Math.min((currentGroup + 1) * 10, 114)}
                </div>
              )}
              <div
                onClick={() => onSurahSelect?.(surah)}
                className={`p-4 rounded-lg cursor-pointer transition-all duration-200 ${
                  selectedSurahId === surah.id
                    ? 'bg-primary-50 dark:bg-primary-700 border-2 border-primary-200 dark:border-primary-500'
                    : 'bg-white dark:bg-gray-800 hover:bg-gray-50 dark:hover:bg-gray-700 border border-gray-200 dark:border-gray-700'
                } sm:p-6 sm:text-lg`}
                tabIndex={selectedSurahId === surah.id ? 0 : -1}
                aria-current={selectedSurahId === surah.id ? 'true' : undefined}
              >
                <div className="flex justify-between items-start">
                  {/* Left side - Surah info */}
                  <div className="flex-1 min-w-0 sm:pl-4">
                    <div className="flex items-center space-x-3 mb-2">
                      {/* Surah number */}
                      <div className="flex-shrink-0 w-8 h-8 bg-primary-500 dark:bg-accent-400 text-white rounded-full flex items-center justify-center text-sm font-semibold sm:w-10 sm:h-10 sm:text-lg">
                        {surah.id}
                      </div>
                      {/* Arabic name */}
                      <h3 
                        className="font-arabic text-xl text-gray-900 dark:text-white truncate"
                        role="heading"
                        aria-level={2}
                      >
                        {surah.name}
                      </h3>
                    </div>
                    {/* English name */}
                    <p 
                      className="text-gray-600 dark:text-gray-300 sm:text-lg mb-1 truncate"
                      aria-label={`Surah ${surah.id} name in English`}
                    >
                      {getSurahName(surah)}
                    </p>
                    {/* Revelation type */}
                    <div className="flex items-center space-x-2">
                      <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
                        surah.revelationType === 'meccan'
                          ? 'bg-blue-100 dark:bg-blue-900/30 text-blue-800 dark:text-blue-300'
                          : 'bg-green-100 dark:bg-green-900/30 text-green-800 dark:text-green-300'
                      }`}>
                        {surah.revelationType === 'meccan' ? ' Meccan' : ' Medinan'}
                      </span>
                    </div>
                  </div>
                  {/* Right side - Stats */}
                  <div className="flex-shrink-0 text-right ml-4">
                    <h3 
                      className="font-semibold text-gray-900 dark:text-white sm:text-xl"
                      role="heading"
                      aria-level={3}
                    >
                      {surah.ayahCount}
                    </h3>
                    <div 
                      className="text-xs text-gray-500 dark:text-gray-400"
                      aria-label={`${surah.ayahCount} verses`}
                    >
                      verses
                    </div>
                  </div>
                </div>
              </div>
            </React.Fragment>
          );
        })}
    </div>
  );
};